.footer {
  padding-top: 64px;
  padding-bottom: 48px;
  background: #04022a;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 56px;

    &__nav {
      img {
        width: 172px;
        height: 25px;
      }

      p {
        margin-top: 32px;
        max-width: 463px;
        color: #bcc3dd;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px; /* 150% */
      }

      nav {
        margin-top: 80px;

        ul {
          display: flex;
          gap: 32px;
          list-style: none;

          li {
            a {
              text-decoration: none;
              color: #fff;
              font-family: Open Sans;
              font-size: 16px;
              font-style: normal;
              font-weight: 600;
              line-height: 24px; /* 150% */
              transition: all 0.2s;

              &:hover {
                color: #b8b8b8;
              }
            }
          }
        }
      }
    }

    &__links {
      p {
        color: #fff;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px; /* 150% */
      }

      &__wrapper {
        margin-top: 16px;
        display: flex;
        flex-direction: column;
        gap: 16px;

        a {
          max-width: 160px;
          min-width: 135px;
          border-radius: 7px;
          transition: all 0.2s;

          &:hover {
            // background: linear-gradient(0deg, rgba(4, 2, 42, 1) 0%, rgba(0, 30, 252, 1) 35%, rgba(0, 212, 255, 1) 100%);
            background: #363555;
          }

          img {
            display: block;
            width: 100%;
          }
        }
      }

      .footer-language-switcher {
        margin-top: 45px;
        position: relative;
        background: transparent;
        padding: 8px 14px;
        border-radius: 100px;
        border: none;
        cursor: pointer;
        transition: background 0.2s;

        &:hover {
          background: #363555;
        }

        .footer-current-language {
          display: flex;
          gap: 12px;
          justify-content: center;
          align-items: center;

          &-icon {
            width: 24px;
            height: 24px;
            border-radius: 100%;
            overflow: hidden;

            img {
              height: 100%;
              width: 100%;
              object-fit: cover;
            }
          }

          &-name {
            font-family: "Open Sans";
            font-weight: 600;
            font-size: 16px;
            line-height: 150%;
            color: #fff;
          }
        }

        .footer-language-list {
          --gap-btn-content: 5px;
          position: absolute;
          list-style-type: none;
          background: #fff;
          bottom: calc(40px + var(--gap-btn-content)); //40 - icon height
          right: 0;

          border-radius: 20px;
          padding: 8px;
          width: 247px;

          box-shadow: 0 0 4px 0 rgba(27, 38, 51, 0.05), 0 0 20px 0 rgba(27, 38, 51, 0.1);

          transition: all 0.2s;

          &::before {
            content: "";
            position: absolute;
            width: inherit;
            height: var(--gap-btn-content);
            bottom: -5px;
            right: -5px;
          }

          li {
            display: flex;
            align-items: center;
            gap: 16px;
            padding: 10px 8px;
            border-radius: 10px;
            background: transparent;

            &:hover {
              background: #e7eef7;
            }

            .language-list-icon {
              position: relative;
              border-radius: 100%;
              width: 24px;
              height: 24px;
              overflow: hidden;

              img {
                height: 100%;
                width: 100%;
                object-fit: cover;
              }
            }

            span {
              font-family: Geologica;
              font-weight: 500;
              font-size: 14px;
              line-height: 171%;
              color: $text-color;
            }
          }
        }
      }
    }
  }

  hr {
    margin-top: 64px;
    width: 100%;
    opacity: 0.3;
    background: #fff;
    height: 1px;
  }

  &__bottom {
    margin-top: 16px;

    .investors {
      display: flex;
      align-items: center;
      gap: 16px;

      span {
        color: #bcc3dd;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
      }

      &__logo {
        position: relative;
        width: fit-content;
        height: 40px;
        overflow: hidden;
        img {
          // width: 200px;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    &__wrapper {
      margin-top: 16px;
      display: flex;
      justify-content: space-between;
      gap: 32px;

      &__rights {
        color: #bcc3dd;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px; /* 150% */
      }

      &__links {
        display: flex;
        flex-wrap: wrap;
        gap: 16px;

        a {
          text-decoration: none;
          color: #bcc3dd;
          font-family: Open Sans;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 24px; /* 150% */
          transition: all 0.2s;

          &:hover {
            color: #fff;
          }
        }
      }
    }
  }
}

@media (max-width: 670px) {
  .footer {
    &__wrapper {
      flex-direction: column;

      nav {
        margin-top: 32px;
      }
      &__links {
        &__wrapper {
          flex-direction: row;
        }

        .footer-language-switcher {
          margin-top: 24px;
          display: block;
          margin-left: auto;
        }
      }
    }

    &__bottom__wrapper {
      flex-direction: column;
    }
  }
}
