@mixin portal {
  // display: flex;
  display: inline-flex;
  align-items: center;
  background: transparent;
  text-decoration: none;
  border-radius: 30px;
  border: 1px solid transparent;
  transition: all 0.2s ease-in-out;

  &:hover,
  &:focus {
    border: 1px solid $text-color;

    .portal__left {
      background: transparent;
      color: $text-color;
      border: 1px solid transparent;
    }
    .portal__right {
      border: 1px solid transparent;
    }
  }
}

@mixin portalLeft {
  display: flex;
  flex: 1 0 0;
  padding: 13px 20px;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  border: 1px solid $text-color;
  background: $text-color;
  color: #fff;

  font-family: Geologica;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: all 0.2s ease-in-out;
}

@mixin portalRight {
  border-radius: 30px;
  border: 1px solid $text-color;
  color: $text-color;
  width: 46px;
  height: 46px;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 22px;
  transition: all 0.2s ease-in-out;

  img {
    width: 18px;
    height: 18px;
  }
}

@mixin titleSpan {
  background-clip: text;
  background-image: linear-gradient(90deg, $text-color, $text-color);
  background-repeat: no-repeat;
  background-size: 0% 100%;
  -webkit-background-clip: text;
  background-color: transparent;
  -webkit-background-image: linear-gradient(90deg, $text-color, $text-color);
  -webkit-background-repeat: no-repeat;
  -webkit-background-size: 0% 100%;
}

@mixin paragraphSpan {
  display: block;
  transform: translateY(100%);
  will-change: transform;
}

@mixin langSwitcher {
  // display: none;
  position: relative;
  background: transparent;
  border-radius: 100px;
  border: none;
  cursor: pointer;
  transition: background 0.2s;

  &:hover {
    background: $grey-color;
  }

  .current-language {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    padding: 14px;
    height: 52px;

    &-icon {
      position: relative;
      border-radius: 100%;
      width: 24px;
      height: 24px;
      overflow: hidden;

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

    &-name {
      color: $text-color;
      font-family: "Open Sans";
      font-size: 16px;
      font-style: normal;
    }
  }

  .current-language.ios {
    padding: 8px;
  }

  .language-list {
    --gap-btn-content: 5px;
    position: absolute;
    list-style-type: none;
    background: #fff;
    top: calc(52px + var(--gap-btn-content));
    left: 0;

    border-radius: 20px;
    padding: 8px;
    width: 247px;

    box-shadow: 0 0 4px 0 rgba(27, 38, 51, 0.05),
      0 0 20px 0 rgba(27, 38, 51, 0.1);

    &::before {
      content: "";
      position: absolute;
      width: inherit;
      height: var(--gap-btn-content);
      top: -5px;
      left: -5px;
    }

    li {
      display: flex;
      align-items: center;
      gap: 16px;
      padding: 10px 8px;
      border-radius: 10px;
      background: transparent;

      &:hover {
        background: #e7eef7;
      }

      .language-list-icon {
        position: relative;
        border-radius: 100%;
        width: 24px;
        height: 24px;
        overflow: hidden;

        img {
          height: 100%;
          width: 100%;
          object-fit: cover;
        }
      }

      span {
        font-family: Geologica;
        font-weight: 500;
        font-size: 14px;
        line-height: 171%;
        color: $text-color;
      }
    }
  }
}
