.about-community {
  position: relative;
  padding-top: clamp(64px, 10vw, 120px);
  padding-bottom: clamp(64px, 10vw, 120px);

  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 48px;
    flex-wrap: wrap;

    &__descr {
      max-width: 550px;

      h4 {
        color: $text-color;
        font-family: Geologica;
        font-size: clamp(22px, 3vw, 24px);
        font-style: normal;
        font-weight: 500;
        line-height: 130%; /* 28.6px */
      }

      h2 {
        margin-top: clamp(24px, 10vw, 127px);
        color: $text-color-hsl;
        font-family: Geologica;
        font-size: clamp(32px, 5vw, 48px);
        font-style: normal;
        font-weight: 500;
        line-height: 130%; /* 41.6px */

        span {
          @include titleSpan;
        }
      }

      p {
        margin-top: clamp(16px, 2vw, 20px);
        color: $secondary-color;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%; /* 24px */
        overflow: hidden;

        span {
          @include paragraphSpan;
        }
      }

      .portal {
        margin-top: 20px;
        @include portal;
        // width: 197px;

        &__left {
          @include portalLeft;
          // width: 147px;
        }

        &__right {
          @include portalRight;
        }
      }
    }

    picture {
      display: block;
      margin: 0 auto;
      img {
        width: clamp(335px, 40vw, 550px);
        flex-shrink: 1;
        aspect-ratio: 1/1;
        border-radius: 20px;
      }
    }
  }
}

@media (max-width: 1195px) {
  .about-community {
    &__wrapper {
      &__descr {
        order: 2;
      }
    }
  }
}
