.services-enterprise {
  padding-top: clamp(127px, 10vw, 202px);
  padding-bottom: clamp(64px, 10vw, 120px);

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 16px;

    &__title {
      h2 {
        max-width: 550px;
        color: $text-color-hsl;
        font-family: Geologica;
        font-size: clamp(32px, 5vw, 48px);
        font-style: normal;
        font-weight: 500;
        line-height: 120%; /* 38.4px */

        span {
          @include titleSpan;
        }
      }

      h6 {
        margin-top: clamp(16px, 3vw, 20px);
        color: $text-color;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%; /* 24px */
      }
    }

    p {
      max-width: 550px;
      color: $secondary-color;
      font-family: Open Sans;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%; /* 24px */
      overflow: hidden;
      span {
        @include paragraphSpan;
      }
    }
  }

  img {
    margin-top: clamp(32px, 5vw, 40px);
    position: relative;
    width: 100%;
    height: clamp(335px, 40vw, 500px);
    border-radius: 20px;
    object-fit: cover;
    transform-origin: 0 0;
    filter: brightness(0.9);
    transition: all 0.3s;
  }
}
