.subscribe {
  background: #0927be;
  padding: 48px 0;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 10px;

    &__title {
      font-family: Geologica;
      font-weight: 500;
      font-size: 20px;
      line-height: 150%;
      color: #fff;
    }

    &__descr {
      margin-top: 8px;
      font-family: "Open Sans";
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: #bcc3dd;
    }

    &__form {
      display: flex;
      align-items: center;
      gap: 16px;
      max-width: 463px;

      input {
        border: 1px solid #d0d5dd;
        border-radius: 8px;
        padding: 10px 14px;
        flex: 1 3;
        height: clamp(46px, 5vw, 52px);

        font-family: "Open Sans";
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: #8d95a6;
      }

      button {
        flex: 0 0;
        border: 1px solid #040829;
        border-radius: 30px;
        padding: 16px 20px;
        height: 52px;
        background: #040829;

        font-family: Geologica;
        font-weight: 400;
        font-size: 16px;
        color: #fff;
      }
    }
  }
}

@media (max-width: 500px) {
  .subscribe {
    &__wrapper {
      &__form {
        button {
          padding: 13px 12px;
        }
      }
    }
  }
}
