.carousel {
  position: relative;
  width: 100vw;
  height: 100dvh;

  &__inner {
    height: 100%;
    &__slide {
      position: absolute;
      left: 100%;
      height: 100%;
      width: 100%;
      z-index: 1;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .slide-0 {
      left: 0;
      img {
        object-position: 70% 50%;
      }
    }
    .slide-1 {
      img {
        object-position: 20% 30%;
      }
    }
    .slide-2 {
      img {
        object-position: 70% 30%;
      }
    }
    .slide-3 {
      img {
        object-position: 70% 50%;
      }
    }
    .slide-4 {
      img {
        object-position: 60% 80%;
      }
    }
    .slide-5 {
      img {
        object-position: 60% 60%;
      }
    }

    &__text {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      opacity: 0;
      z-index: 10;

      h2 {
        font-family: Geologica;
        font-weight: 500;
        font-size: clamp(36px, 6vw, 58px);
        line-height: 120%;
        color: #fff;
      }

      a {
        position: relative;
        margin-top: 20px;
        text-decoration: none;
        border: none;
        width: fit-content;
        height: 57px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 8px;

        font-family: Geologica;
        font-weight: 400;
        font-size: 20px;
        color: #fff;
        cursor: pointer;
        transition: all 0.2s;

        span {
          position: relative;
          &:after {
            content: "";
            position: absolute;
            bottom: 0px;
            left: 0;
            width: 0;
            height: 2px;
            background: #fff;
            transition: all 0.2s;
          }
        }

        img {
          height: 24px;
          width: 24px;
          transition: rotate 0.2s;
        }

        &:hover {
          span {
            &:after {
              width: 100%;
            }
          }
          img {
            rotate: 45deg;
          }
        }
      }
    }

    .text-0 {
      opacity: 1;
      z-index: 15;
    }
  }

  &__navigation {
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    padding: 24px 24px 20px;
    border-radius: 20px;
    height: 78px;
    width: min(566px, 90vw);
    // height: 80px;
    backdrop-filter: blur(114px);
    background: rgba(4, 8, 41, 0.05);
    overflow: hidden;
    z-index: 10;

    .progress-bar {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 4px;

      .progress {
        height: 100%;
        background: $blue;
        width: 0;
        transform-origin: 0 0;
      }
    }

    &__controls {
      position: relative;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: clamp(4px, 2vw, 24px);

      a {
        text-decoration: none;
        position: relative;
        height: 100%;
        flex: 1;
        display: flex;
        align-items: center;
        gap: 6px;
        background: transparent;
        border: none;

        &:hover {
          .btnNumber {
            opacity: 1 !important;
          }
          .btnText {
            opacity: 1 !important;
          }
        }

        &:after {
          content: "";
          position: absolute;
          height: 100%;
          width: 1px;
          background: rgba(255, 255, 255, 0.4);
          right: 0;
          top: 0;
        }

        &:last-of-type {
          &:after {
            display: none;
          }
        }

        .btnNumber {
          font-family: Geologica;
          font-weight: 300;
          font-size: 20px;
          line-height: 130%;
          color: #fff;
          opacity: 0.5;
        }

        .btnText {
          font-family: Geologica;
          font-weight: 500;
          font-size: 14px;
          line-height: 130%;
          color: #fff;
          text-align: left;
          opacity: 0.5;
        }
      }
    }
  }
}

@media (max-width: 565px) {
  .carousel {
    &__inner {
      .container {
        max-width: 100%;
      }
      &__text {
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        width: 100%;

        a {
          margin: 20px auto 0;
        }
      }
    }
    &__navigation {
      padding: 12px 8px 8px;
      &__controls {
        align-items: flex-start;
        a {
          flex-direction: column;
          justify-content: space-between;
          padding-right: 4px;
          height: 100%;
          .btnText {
            text-align: center;
            line-height: 110%;
          }
        }
      }
    }
  }
}
