.join {
  padding-top: clamp(40px, 10vw, 48px);
  padding-bottom: clamp(40px, 10vw, 48px);
  background: #0927be;

  &__wrapper {
    display: flex;
    justify-content: space-between;

    &__info {
      h3 {
        color: #fff;
        font-family: Geologica;
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: 30px; /* 150% */
      }

      p {
        margin-top: 8px;
        color: #bcc3dd;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px; /* 150% */
      }
    }

    &__form {
      display: flex;
      gap: 16px;

      input {
        color: var(--Secondary, #8d95a6);
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px; /* 150% */
        border-radius: 8px;
        border: 1px solid var(--Gray-300, #d0d5dd);
        background: var(--White, #fff);
        padding: 14px;
        height: clamp(46px, 5vw, 52px);
        width: clamp(218px, 40vw, 330px);
      }

      button {
        height: clamp(46px, 5vw, 52px);
        width: clamp(101px, 15vw, 117px);
        background: #040829;
        border-radius: 30px;
        border: 1px solid #040829;
        padding-top: clamp(13px, 2vw, 16px);
        padding-bottom: clamp(13px, 2vw, 16px);
        padding-left: clamp(12px, 2vw, 20px);
        padding-right: clamp(12px, 2vw, 20px);
        color: #fff;
        font-family: Geologica;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
          background: #f0f4f8;
          color: $text-color;
        }
      }
    }
  }
}

@media (max-width: 995px) {
  .join {
    &__wrapper {
      flex-direction: column;
      gap: 24px;
    }
  }
}
