.intouch {
  position: relative;
  padding-top: clamp(64px, 10vw, 120px);
  padding-bottom: clamp(64px, 10vw, 120px);

  &__wrapper {
    display: flex;
    justify-content: space-between;

    &__info {
      h2 {
        color: $text-color-hsl;
        font-family: Geologica;
        font-size: clamp(32px, 5vw, 44px);
        font-style: normal;
        font-weight: 500;
        line-height: 130%; /* 41.6px */

        span {
          // @include titleSpan;
          color: $text-color;
        }
      }

      p {
        margin-top: clamp(16px, 3vw, 20px);
        margin-bottom: 40px;
        max-width: 440px;
        color: $secondary-color;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%; /* 24px */
      }

      &__block {
        margin-top: 20px;
        display: flex;
        gap: 10px;
        align-items: center;

        &__icon {
          width: 44px;
          height: 44px;
          border-radius: 100px;
          background: #f0f4f8;
          padding: 10px;
        }

        p {
          margin: 0;
          max-width: 300px;
          color: $text-color;
          font-family: Geologica;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 150%; /* 24px */
        }
      }
    }

    &__form {
      padding-top: clamp(30px, 3vw, 40px);
      padding-bottom: clamp(30px, 3vw, 40px);
      padding-left: clamp(20px, 3vw, 40px);
      padding-right: clamp(20px, 3vw, 40px);
      border-radius: 12px;
      background: #f0f4f8;
      //   width: clamp(335px, 40vw, 590px);
      max-width: 590px;
      width: 100%;

      label {
        color: #344054;
        font-family: Geologica;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 20px; /* 142.857% */
      }

      input {
        display: block;
        width: 100%;
        margin-top: 6px;
        margin-bottom: 10px;
        color: $secondary-color;
        font-family: Open Sans;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px; /* 171.429% */
        border-radius: 8px;
        border: 1px solid #d0d5dd;
        padding: 10px 14px;
      }

      .portal {
        padding: 0;
        @include portal;
        margin-top: clamp(16px, 3vw, 20px);
        cursor: pointer;

        &__left {
          @include portalLeft;
        }

        &__right {
          @include portalRight;
        }
      }
    }
  }
}

@media (max-width: 1240px) {
  .intouch {
    &__wrapper {
      flex-direction: column;

      &__form {
        margin-top: 42px;
      }
    }
  }
}

.contact {
  .intouch {
    padding-top: clamp(127px, 10vw, 202px);
  }
}
