.about-mission {
  position: relative;
  padding-top: clamp(127px, 10vw, 202px);
  padding-bottom: clamp(64px, 10vw, 120px);

  &__text {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    justify-content: space-between;

    h2 {
      color: $text-color-hsl;
      font-family: Geologica;
      font-size: clamp(32px, 5vw, 48px);
      font-style: normal;
      font-weight: 500;
      line-height: 130%; /* 62.4px */

      span {
        @include titleSpan;
      }
    }

    p {
      max-width: 570px;
      color: $secondary-color;
      font-family: Open Sans;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%; /* 24px */
      overflow: hidden;

      span {
        @include paragraphSpan;
      }
    }
  }

  &__wrapper {
    margin-top: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: clamp(16px, 3vw, 40px);

    &__block {
      padding: 20px;
      // padding-bottom: 0;
      display: flex;
      flex-direction: column;
      border-radius: 20px;
      background: #f0f4f8;
      width: 265px;
      // height: 240px;

      transform: translateY(100%);
      opacity: 0;

      img {
        width: 32px;
        height: 32px;
      }

      h3 {
        margin-top: 69px;
        color: $text-color;
        font-family: Geologica;
        font-size: clamp(22px, 3vw, 24px);
        font-style: normal;
        font-weight: 500;
        line-height: 130%; /* 28.6px */
      }

      p {
        margin-top: 10px;
        color: $secondary-color;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%; /* 24px */
      }
    }
  }
}

@media (max-width: 608px) {
  .about-mission {
    &__wrapper {
      margin-top: 32px;

      &__block {
        width: 335px;
        // height: 181px;

        h3 {
          margin-top: 40px;
        }
      }
    }
  }
}
