.steps {
  padding-top: clamp(64px, 10vw, 120px);

  h2 {
    margin: 0 auto;
    color: $text-color-hsl;
    font-family: Geologica;
    font-size: clamp(32px, 5vw, 48px);
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 41.6px */
    max-width: 540px;

    span {
      @include titleSpan;
    }
  }

  &__mobileWrapper {
    margin-top: 32px;

    &__block {
      margin: 0 auto;
      display: flex;
      gap: 8px;
      max-width: 510px;

      &__left {
        margin-top: 2px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .circle {
          width: 16px;
          height: 16px;
          background-color: white;
          outline: 1px solid #cdced4;
          border-radius: 50%;
          padding: 4px;

          .innerCircle {
            width: 8px;
            height: 8px;
            background-color: $blue;
            border-radius: 50%;
          }
        }

        .line {
          width: 1px;
          height: 100%;
          background-color: #cdced4;
        }
      }

      &__right {
        h4 {
          color: $text-color;
          font-family: Geologica;
          font-size: clamp(16px, 2vw, 18px);
          font-style: normal;
          font-weight: 500;
          line-height: 130%; /* 20.8px */
        }

        img {
          margin-top: 16px;
          width: clamp(311px, 60vw, 400px);
          aspect-ratio: 2.075 / 1;
          border-radius: 20px;
          object-fit: cover;
          object-position: top center;
        }

        h3 {
          margin-top: 16px;
          color: $text-color;
          font-family: Geologica;
          font-size: clamp(22px, 3vw, 32px);
          font-style: normal;
          font-weight: 500;
          line-height: 130%; /* 28.6px */
        }

        p {
          margin-top: 16px;
          margin-bottom: 32px;
          color: $secondary-color;
          font-family: Open Sans;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 150%; /* 24px */
        }
      }
    }

    .block3 {
      .line {
        display: none;
      }
    }
  }

  &__desktopWrapper {
    position: relative;
    margin-top: 60px;
    padding-bottom: 140px;

    &__row {
      display: flex;
      justify-content: space-between;
      align-items: center;

      img {
        width: clamp(350px, 40vw, 510px);
        aspect-ratio: 1.214 / 1;
        border-radius: 20px;
      }

      &__descr {
        max-width: 510px;

        transform: translateY(100%);
        opacity: 0;

        h4 {
          color: $text-color;
          font-family: Geologica;
          font-size: 24px;
          font-style: normal;
          font-weight: 500;
          line-height: 130%; /* 31.2px */
        }

        h3 {
          margin-top: 20px;
          color: $text-color;
          font-family: Geologica;
          font-size: 32px;
          font-style: normal;
          font-weight: 500;
          line-height: 130%; /* 41.6px */
        }

        p {
          margin-top: 20px;
          color: $secondary-color;
          font-family: Open Sans;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 150%; /* 24px */
        }
      }
    }

    &__row:not(:first-child) {
      margin-top: 60px;
    }

    .line {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    .showRow {
      transform: translateY(0);
      opacity: 1;
    }
  }
}

@media (min-width: 650px) {
  .steps {
    h2 {
      text-align: center;
    }
  }
}
@media (min-width: 1280px) {
  .steps {
    &__mobileWrapper {
      display: none;
    }
  }
}
@media (max-width: 1279px) {
  .steps {
    &__desktopWrapper {
      display: none;
    }
  }
}
