.services-why {
  position: relative;
  padding-top: clamp(64px, 10vw, 120px);
  padding-bottom: clamp(64px, 10vw, 120px);

  picture {
    img {
      position: absolute;
      top: 0;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  .container {
    display: flex;
    justify-content: space-between;
    gap: 32px;
    flex-wrap: wrap;
  }

  &__title {
    h2 {
      color: $text-color-hsl;
      font-family: Geologica;
      font-size: clamp(32px, 5vw, 48px);
      font-style: normal;
      font-weight: 500;
      line-height: 130%; /* 41.6px */

      span {
        @include titleSpan;
      }
    }

    p {
      margin-top: clamp(16px, 3vw, 20px);
      max-width: 327px;
      color: $secondary-color;
      font-family: Open Sans;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%; /* 24px */
    }
  }

  &__wrapper {
    max-width: 773px;

    hr {
      width: 100%;
      background: rgba(4, 8, 41, 0.2);
      margin-bottom: 30px;
      &:not(:first-of-type) {
        margin-top: 30px;
      }
    }

    &__block {
      display: flex;
      align-items: start;

      opacity: 0;
      transform: translateX(100%);

      &__name {
        display: flex;
        align-items: center;
        gap: clamp(16px, 3vw, 44px);

        h4 {
          color: $text-color;
          font-family: Geologica;
          font-size: clamp(22px, 3vw, 24px);
          font-style: normal;
          font-weight: 500;
          line-height: 150%; /* 33px */
        }

        h3 {
          width: 295px;
          color: $text-color;
          font-family: Geologica;
          font-size: clamp(26px, 3vw, 32px);
          font-style: normal;
          font-weight: 500;
          line-height: 1;
        }
      }

      &__descr {
        max-width: 367px;
        color: $secondary-color;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%; /* 24px */
      }
    }
  }
}

@media (max-width: 1120px) {
  .services-why {
    &__wrapper {
      margin: 0 auto;
    }
  }
}

@media (max-width: 735px) {
  .services-why {
    .container {
      flex-direction: column;
    }

    &__wrapper {
      &__block {
        flex-direction: column;

        h3 {
          width: auto;
        }
      }
    }
  }
}
