h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

.container {
  position: relative;
  max-width: min(1180px, 90vw);
  height: 100%;
  margin: 0 auto;
}

body {
  position: relative;
  scroll-behavior: smooth;
}

.about,
.services,
.contact {
  .desktopHeader {
    nav {
      background: $grey-color;
    }
  }
}

.home {
  .desktopHeader .wrapper-right {
    .language-switcher {
      background: $grey-color;
    }
  }
}

section {
  overflow: hidden;
}
