.team {
  user-select: none;
  position: relative;
  padding-top: clamp(64px, 10vw, 120px);
  padding-bottom: clamp(64px, 10vw, 120px);
  background: #f8f8f7;

  h2 {
    color: $text-color-hsl;
    font-family: Geologica;
    font-size: clamp(32px, 5vw, 42px);
    max-width: 450px;
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 41.6px */

    span {
      @include titleSpan;
    }
  }

  p {
    margin-top: clamp(16px, 3vw, 20px);
    max-width: 468px;
    color: $secondary-color;
    font-family: Open Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */
    overflow: hidden;

    span {
      @include paragraphSpan;
    }
  }

  .portal {
    @include portal;
    margin-top: clamp(24px, 3vw, 40px);
    max-width: 220px;

    &__left {
      @include portalLeft;
    }

    &__right {
      @include portalRight;
    }
  }

  &__wrapper {
    margin-top: clamp(48px, 10vw, 77px);
    display: flex;
    gap: clamp(15px, 3vw, 80px);

    h3 {
      color: $blue;
      font-family: Geologica;
      font-size: clamp(56px, 10vw, 80px);
      font-style: normal;
      font-weight: 500;
      line-height: 130%; /* 104px */
    }

    p {
      margin-top: 0;
      color: $text-color;
      text-align: center;
      font-family: Open Sans;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%; /* 24px */
      text-align: center;
    }

    &__countries {
      position: relative;

      &:before,
      &:after {
        content: "";
        position: absolute;
        background: rgba(4, 8, 41, 0.2);
        width: 1px;
        height: 100%;
      }

      &::before {
        top: 0;
        left: -40px;
      }

      &::after {
        top: 0;
        right: -40px;
      }
    }
  }

  #map {
    position: absolute;
    right: -140px;
    top: 50%;
    transform: translateY(-50%);
    width: 690px;
    min-width: 272px;
    aspect-ratio: 1.15 / 1;
  }
}

@media (max-width: 1280px) {
  .team {
    &__wrapper {
      justify-content: center;
    }

    #map {
      margin: 50px auto 0 auto;
      position: static;
      transform: translateY(0);
    }
  }
}

@media (max-width: 1785px) {
  .team__wrapper__countries::before,
  .team__wrapper__countries::after {
    display: none;
  }
}
