.textAnimation {
  background-size: 100% 100% !important;
}

.y0 {
  transform: translateY(0) translateZ(0) !important;
}

.moveUpAndFadeIn {
  transform: translateY(0) !important;
  opacity: 1 !important;
}

.moveLeftAndFadeIn {
  opacity: 1 !important;
  transform: translateX(0) !important;
}
