.access {
  position: relative;
  padding-top: clamp(64px, 10vw, 120px);
  padding-bottom: clamp(64px, 10vw, 120px);
  overflow: hidden;

  picture {
    img {
      position: absolute;
      top: 0;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  h2 {
    max-width: 540px;
    color: $text-color-hsl;
    font-family: Geologica;
    font-size: clamp(32px, 5vw, 48px);
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 62.4px */

    span {
      @include titleSpan;
    }
  }

  &__wrapper {
    margin-top: clamp(16px, 3vw, 20px);
    display: flex;
    justify-content: space-between;
    gap: 16px;

    &__left {
      max-width: 565px;

      p {
        max-width: 508px;
        color: $secondary-color;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%; /* 24px */
      }

      img {
        // width: 100%;
        display: block;
        margin: 0 auto;
        max-width: min(80%, 565px);
      }
    }

    &__right {
      &__block {
        display: flex;
        gap: 20px;
        opacity: 0;
        transform: translateX(100%);

        &:not(:first-child) {
          margin-top: 40px;
        }

        &__left {
          color: $text-color;
          font-family: Geologica;
          font-size: 24px;
          font-style: normal;
          font-weight: 500;
          line-height: 150%;
        }

        &__right {
          max-width: 433px;

          h3 {
            color: $text-color;
            font-family: Geologica;
            font-size: clamp(22px, 3vw, 24px);
            font-style: normal;
            font-weight: 500;
            line-height: 150%; /* 36px */
          }
          p {
            color: $secondary-color;
            font-family: Open Sans;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 150%; /* 24px */
          }
        }
      }
    }
  }
}

@media (max-width: 1100px) {
  .access {
    &__wrapper {
      flex-direction: column;

      &__left {
        margin: 0 auto;
      }

      &__right {
        margin: 0 auto;
      }

      &__right__block__left {
        display: none;
      }
    }
  }
}
