.mobileHeader {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding-top: 24px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  &__logo {
    width: 170px;
    height: 28px;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    gap: 6px;
  }

  .iconsWrapper {
    position: relative;
    width: 39px;
    height: 39px;
  }

  &__menu-icon {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
  }

  .language-switcher {
    @include langSwitcher;

    .language-list {
      left: -125px;
    }
  }
}

@media (min-width: 1100px) {
  .mobileHeader {
    display: none;
  }
}
