.about-support {
  position: relative;
  background: $grey-color;
  padding-top: clamp(64px, 10vw, 120px);
  padding-bottom: clamp(64px, 10vw, 120px);

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 16px;

    h2 {
      max-width: 550px;
      color: $text-color-hsl;
      font-family: Geologica;
      font-size: clamp(32px, 5vw, 48px);
      font-style: normal;
      font-weight: 500;
      line-height: 120%; /* 38.4px */

      span {
        @include titleSpan;
      }
    }

    p {
      max-width: 550px;
      color: $secondary-color;
      font-family: Open Sans;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%; /* 24px */
      overflow: hidden;

      span {
        @include paragraphSpan;
      }
    }
  }

  a {
    margin-top: clamp(32px, 5vw, 40px);
    position: relative;
    width: 100%;
    height: clamp(335px, 40vw, 500px);
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    overflow: hidden;

    &:hover {
      & > img {
        scale: 1.2;
        filter: brightness(1);
      }
      .portal {
        border: 1px solid $text-color;

        .portal__left {
          background: transparent;
          color: $text-color;
          border: 1px solid transparent;
        }
        .portal__right {
          border: 1px solid transparent;
        }
      }
    }

    & > img {
      height: 100%;
      border-radius: 20px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      object-fit: contain;
      transform-origin: 0 0;
      filter: brightness(0.9);
      transition: all 0.3s;
    }

    .portal {
      @include portal;
      z-index: 1;

      &__left {
        @include portalLeft;
      }

      &__right {
        @include portalRight;
      }
    }
  }
}
