.services-pros {
  background: $grey-color;
  padding-top: clamp(64px, 10vw, 120px);
  padding-bottom: clamp(64px, 10vw, 120px);

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 16px;

    h2 {
      max-width: 550px;
      color: $text-color-hsl;
      font-family: Geologica;
      font-size: clamp(32px, 5vw, 48px);
      font-style: normal;
      font-weight: 500;
      line-height: 120%; /* 38.4px */

      span {
        @include titleSpan;
      }
    }

    &__description {
      max-width: 570px;
      min-width: 335px;
      display: flex;
      flex-direction: column;
      transform: translateY(100%);
      opacity: 0;
      h5 {
        font-family: Geologica;
        font-weight: 500;
        font-size: clamp(16px, 2vw, 24px);
        line-height: 130%;
        color: $text-color;
      }

      h6 {
        margin-top: 10px;
        font-family: "Open Sans";
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: $secondary-color;
      }
    }
  }

  &__list {
    margin-top: clamp(32px, 5vw, 80px);
    width: 100%;

    &__block {
      margin-top: 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      padding-top: 30px;
      border-top: 1px solid rgba(4, 8, 41, 0.2);

      &__left {
        display: flex;
        align-items: center;
        flex-shrink: 1;
        &__number {
          margin-right: clamp(16px, 5vw, 80px);
          font-family: Geologica;
          font-weight: 500;
          font-size: 24px;
          line-height: 130%;
          color: $text-color;
        }

        &__title {
          font-family: Geologica;
          font-weight: 500;
          font-size: clamp(24px, 3vw, 32px);
          line-height: 130%;
          color: $text-color;
        }
      }

      &__descr {
        flex-shrink: 3;
        max-width: 570px;
        min-width: 200px;
        font-family: "Open Sans";
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: $secondary-color;
        text-align: right;
      }
    }
  }

  &__pricing {
    &__desktop {
      margin-top: 80px;
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      justify-content: space-around;

      .pricing-card {
        border-radius: 20px;
        padding: 30px 40px;
        width: 367px;
        height: 428px;
        background: #fff;

        &__icon {
          width: 32px;
          height: 32px;
        }

        &__title {
          margin-top: clamp(8px, 2vw, 32px);
          font-family: Geologica;
          font-weight: 500;
          font-size: 24px;
          line-height: 130%;
          color: $text-color;
        }

        &__descr {
          margin-top: 24px;
          font-family: "Open Sans";
          font-weight: 400;
          font-size: 16px;
          line-height: 150%;
          color: $secondary-color;
        }
      }
    }
  }
}

@media (max-width: 520px) {
  .services-pros__list__block {
    flex-direction: column;
    align-items: initial;

    &__descr {
      text-align: left;
    }
  }
}

@media (max-width: 745px) {
  .services-pros__pricing__desktop {
    .pricing-card {
      padding: 24px 20px;
      height: auto;
    }
  }
}
