.email-verified {
  padding: 24px;
  width: 100vw;
  height: 100dvh;
  background: #e3ebf3;
  display: flex;
  justify-content: center;
  align-items: center;

  .wrapper {
    padding: 40px 0;
    padding-left: clamp(32px, 5vw, 80px);
    padding-right: clamp(32px, 5vw, 80px);
    max-width: 654px;
    background-color: #fff;
    border-radius: 20px;

    img {
      display: block;
      margin: 0 auto;
    }

    h2 {
      margin-top: 32px;
      color: $text-color;

      text-align: center;
      font-family: Geologica;
      font-size: clamp(20px, 3vw, 24px);
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }

    h4 {
      margin-top: 16px;
      color: $text-color;
      font-family: Open Sans;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 28px; /* 175% */
      text-align: center;
    }

    a {
      margin-top: 32px;
      width: 100%;
      height: 52px;
      border-radius: 30px;
      border: 1px solid #040829;
      background: #040829;

      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      text-decoration: none;
      font-family: Geologica;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;

      &:hover {
        border: 1px solid #0c1672;
        background: #0c1672;
      }
    }

    h5 {
      margin-top: 32px;
      color: #8d95a6;
      text-align: center;
      font-family: Open Sans;
      font-size: 13px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }
}
