.contract-text {
  .container {
    max-width: 720px;
  }
  padding-bottom: clamp(64px, 10vw, 120px);

  h2 {
    margin-top: 48px;
    margin-bottom: 24px;
    color: $text-color;
    font-family: Geologica;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
    line-height: 38px;
  }

  h5 {
    color: $text-color;
    font-family: Geologica;
    font-size: clamp(12px, 2vw, 14px);
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    margin-bottom: 16px;
  }

  p,
  ol {
    color: #8d95a6;
    font-family: Open Sans;
    font-size: clamp(16px, 2vw, 18px);
    font-style: normal;
    font-weight: 400;
    line-height: 28px; /* 155.556% */
  }

  ul {
    margin-left: 20px;
  }

  h3 {
    margin-top: clamp(32px, 5vw, 48px);
    margin-bottom: clamp(16px, 5vw, 24px);
    color: $text-color;
    font-family: Geologica;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
  }
}
