.desktopHeader {
  padding-top: 24px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;

  &__nav {
    // width: 388px;
    height: 52px;
    flex-shrink: 0;
    border-radius: 50px;
    background: #f0f4f8;
    padding: 8px;

    ul {
      height: 100%;
      display: flex;
      li {
        height: 100%;
        list-style: none;
        border-radius: 30px;
        transition: all 0.3s;

        a {
          display: flex;
          align-items: center;
          padding: 0 20px;
          width: 100%;
          height: 100%;
          color: #171717;
          font-family: Open Sans;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 130%; /* 20.8px */
          text-transform: capitalize;
          text-decoration: none;
          transition: all 0.2s;
        }

        &:hover {
          background-color: $blue;
          a {
            color: #fff;
          }
          svg path {
            fill: #fff;
          }
        }
      }
    }
  }

  .wrapper-right {
    display: flex;
    align-items: center;
    gap: 6px;

    .language-switcher {
      @include langSwitcher;
    }
  }

  .portal {
    @include portal;

    &__left {
      @include portalLeft;
    }

    &__right {
      @include portalRight;
    }
  }
}

@media (max-width: 1100px) {
  .desktopHeader,
  .headerBG {
    display: none;
  }
}

.headerBG {
  width: 100%;
  height: 98px;
  position: fixed;
  z-index: 10;
  background: rgba(4, 8, 41, 0.05);
  backdrop-filter: blur(10px);
  // box-shadow: 0 0 8px 2px rgba($color: #fff, $alpha: 0.1) inset;
}

.headContainer {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: auto;
  left: 50%;
  transform: translateX(-50%);
}

.hidden-lang-list {
  visibility: none;
  opacity: 0;
  pointer-events: none;
}
