.services-freelancer {
  padding-top: clamp(64px, 10vw, 120px);
  padding-bottom: clamp(64px, 10vw, 120px);

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 16px;

    h2 {
      max-width: 550px;
      color: $text-color-hsl;
      font-family: Geologica;
      font-size: clamp(32px, 5vw, 48px);
      font-style: normal;
      font-weight: 500;
      line-height: 120%; /* 38.4px */

      span {
        @include titleSpan;
      }
    }

    &__description {
      max-width: 570px;
      min-width: 335px;
      display: flex;
      flex-direction: column;
      transform: translateY(100%);
      opacity: 0;
      h5 {
        font-family: Geologica;
        font-weight: 500;
        font-size: 24px;
        line-height: 130%;
        color: $text-color;
      }

      h6 {
        margin-top: 10px;
        font-family: "Open Sans";
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: $secondary-color;
      }
    }
  }

  &__pros {
    margin-top: clamp(48px, 5vw, 80px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px;

    &__card {
      position: relative;
      padding: 40px;
      border-radius: 20px;
      width: clamp(335px, 40vw, 367px);
      height: 400px;
      background: $grey-color;

      &__title {
        font-family: Geologica;
        font-weight: 500;
        font-size: clamp(22px, 3vw, 24px);
        line-height: 130%;
        color: $text-color;
      }

      &__descr {
        margin-top: 10px;
        font-family: "Open Sans";
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: $secondary-color;
      }

      &__icon {
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: translateX(-50%);
        transform-origin: left bottom;
      }
    }
  }

  &__linksWrapper {
    margin: 0 auto;
    margin-top: clamp(64px, 10vw, 90px);
    max-width: 697px;
    &__title {
      font-family: Geologica;
      font-weight: 500;
      font-size: clamp(22px, 3vw, 32px);
      line-height: 130%;
      text-align: center;
      color: $text-color;
    }
    &__descr {
      margin-top: 10px;
      font-family: "Open Sans";
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: $secondary-color;
    }
    &__links {
      margin-top: 20px;
      display: flex;
      justify-content: center;
      gap: 20px;

      &__link {
        position: relative;
        background: none;
        border: none;
        border-radius: 40px;
        width: clamp(160px, 25vw, 178px);
        height: clamp(46px, 5vw, 50px);
        cursor: pointer;
        transition: all 0.2s;
        transform: translateZ(0);

        svg {
          height: 100%;
          display: block;
          width: 100%;
        }

        &:hover {
          filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.45)) brightness(1.5);
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .services-freelancer {
    &__pros {
      flex-direction: column;
      align-items: center;
      gap: 16px;

      &__card {
        padding: 24px 20px;
        width: 335px;
        height: 320px;

        &__icon {
          scale: 0.7;
        }
      }
    }
  }
}
