.steps.advocate {
  padding-bottom: clamp(64px, 10vw, 120px);
  .advocate__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 20px;

    h2 {
      text-align: left;
    }

    &__description {
      max-width: 570px;
      min-width: 335px;
      overflow: hidden;

      div {
        transform: translateY(100%);
        opacity: 0;
      }

      h5 {
        font-family: Geologica;
        font-weight: 500;
        font-size: 24px;
        line-height: 130%;
        color: $text-color;
      }

      h6 {
        margin-top: 10px;
        font-family: "Open Sans";
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: $secondary-color;
      }
    }
  }

  .steps__desktopWrapper {
    &__row:not(:first-child) {
      margin-top: 0;
    }
    &__row {
      img {
        aspect-ratio: 1.594 / 1;
      }
    }
  }

  .steps__mobileWrapper {
    padding-bottom: 64px;
    .block3 {
      .line {
        display: block;
      }
    }
    .block4 {
      .line {
        display: none;
      }
    }
  }

  .line {
    top: 151px;
    transform: translate(-50%, 0);
  }

  .advocate__linksWrapper {
    margin: 0 auto;
    max-width: 697px;
    &__title {
      font-family: Geologica;
      font-weight: 500;
      font-size: clamp(22px, 3vw, 32px);
      line-height: 130%;
      text-align: center;
      color: $text-color;
    }
    &__descr {
      margin-top: 10px;
      font-family: "Open Sans";
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: $secondary-color;
    }
    &__links {
      margin-top: 20px;
      display: flex;
      justify-content: center;
      gap: 20px;

      &__link {
        position: relative;
        background: none;
        border: none;
        border-radius: 40px;
        width: clamp(160px, 25vw, 178px);
        height: clamp(46px, 5vw, 50px);
        cursor: pointer;
        transition: all 0.2s;
        transform: translateZ(0);

        svg {
          height: 100%;
          display: block;
          width: 100%;
        }

        &:hover {
          filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.45)) brightness(1.5);
        }
      }
    }
  }
}

@media (max-width: 1256px) {
  .advocate.steps {
    .advocate__wrapper {
      flex-direction: column;
    }
  }
}

@media (max-width: 710px) {
  .steps.advocate {
    .advocate__linksWrapper__title {
      text-align: left;
    }
  }
}
